// Components
import React from 'react';
import { useSelector } from 'react-redux';
// Components
// import { Footer } from '../../components/Footer';
import { Header } from '../../components/Header';
import { Link } from 'react-router-dom';
// Css
import './index.css'

import styled from 'styled-components'

export const WelcomePage = () => {
  const [isOpenModal, setIsOpenModal] = React.useState(false);
  const { currentUser } = useSelector(state => state.user);
  const handleOpenModal = () => {
    setIsOpenModal(true);
  };

  const WelcomeMessage = styled.div`
    font-size: 3rem;
    margin-bottom: 5rem;
    font-style: italic;
  `

  return (
    <div>
      <Header isOpenModal={isOpenModal} setIsOpenModal={setIsOpenModal} />

      <div>
        <section className='section-banner'>
          <div className='container'>
            <div className='banner__box'>
              <h1 className='banner__box__title'>
                SINH TRC BNH L CY TRNG
              </h1>
              <WelcomeMessage>
                {currentUser ? "Cho mng tr li, " + currentUser.name : ""}
              </WelcomeMessage>
              <p className='banner__box__content'>
                Phn tch nhanh bnh l ca cy trng, tm ra gii php iu tr
                ph hp  tng nng sut cho cy

              </p>
              {currentUser ? "" : <div className='banner__box__btn'>
                <span className="banner__btn" onClick={handleOpenModal}>BT U</span>
              </div>}

            </div>
          </div>
        </section>

        <div className='section-intro'>
          <div className='intro__title'>
            <h2>"Nu ai cng c th trng mt ci cy trong cuc i
              th ni c n ch cn l a tr ca nim vui"</h2>
            <p>-Trng mt ci cy trong cuc i-</p>
          </div>

          <div className='container wrap-box-intro'>
            <div className='intro__box'>
              <Link to='/du-doan-hinh-anh'>
                <h4 className='intro__box-title'>D on</h4>
              </Link>
              <p className='intro__box-content'>
                S dng tr tu nhn to  d on bnh cho cy thng qua hnh nh bn chp
              </p>
            </div>
            <div className='intro__box'>
              <Link to='/forum '>
                <h4 className='intro__box-title'>Din n</h4>
              </Link>
              <p className='intro__box-content'>
                Ni bn c th giao lu hi p vi nhng ngi khc c chung tnh yu vi sc khe cy trng
              </p>
            </div>
            <div className='intro__box'>
              <h4 className='intro__box-title'> xut</h4>
              <p className='intro__box-content'>
                S dng tr tu nhn to   xut cy trng hoc phn bn ph hp cho t trng ca bn
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* </> */}
    </div>
  );
};
